import Vue from "vue";
import Router from "vue-router";
Vue.use(Router);
import SupporterList from "@/components/SupporterList";
import SupporterAssign from "@/components/SupporterAssign";
import dashBoard from "@/components/dashBoard";
import home from "@/components/home";


export default new Router({
  routes: [
    {
      path: "/SupporterList",
      name: "SupporterList",
      component: SupporterList
    },
    {
      path: "/SupporterAssign",
      name: "SupporterAssign",
      component: SupporterAssign
    },
    {
      path: "/",
      name: "home",
      component: home
    },
    {
      path: "/dashBoard",
      name: "dashBoard",
      component: dashBoard
    },
  
    
  ]
});



