<template>
  <div>
    <div class="md-layout" style="display: block; color:#F6F6F6">
      <div class="supportermain" style="min-height: 600px;">
        <md-content
          class="md-elevation-10"
          style="marginTop: 10%; marginLeft: 25%; width: 50%; height: 300px; "
        >
          <div v-if="!view_info">아직 등록이 되지 않았습니다. 아래의 버튼을 눌러 서포터즈 등록을 진행해주세요</div>
          <div v-else style="text-align:left;font-weight: bold;">등록이 완료되었습니다.</div>
        </md-content>

        <md-speed-dial :class="bottomPosition" style="marginRight:20%; marginBottom:8%">
          <md-button class="md-fab md-primary" v-on:click="active = true">
            <img src="../assets/icon-add.svg" />
          </md-button>
        </md-speed-dial>
        <!-- =============서포터즈 submit 양식 ================== -->
        <md-dialog :md-active.sync="active" style="width : 800px; height: 1300px ">
          <md-dialog-title>서포터즈 신청</md-dialog-title>
          <md-field class="select">
            <label>신청자 이름</label>
            <md-input v-model="submit.name"></md-input>
          </md-field>
          <md-field class="select">
            <label>신청자 이메일</label>
            <md-input v-model="submit.email"></md-input>
          </md-field>
          <md-field class="select">
            <label>신청자 연락처</label>
            <md-input v-model="submit.contact"></md-input>
          </md-field>
          <md-field class="select">
            <label>신청자 홈페이지 주소</label>
            <md-input v-model="submit.homepage"></md-input>
          </md-field>
          <md-field class="select">
            <label>주최기관</label>
            <md-input v-model="submit.host"></md-input>
          </md-field>
          <md-field class="select">
            <input v-on:change="fileSelect()" type="file" ref="img_path" />
          </md-field>
          <md-field class="select">
            <label>설명글</label>
            <md-textarea v-model="submit.text"></md-textarea>
          </md-field>
          <md-dialog-actions>
            <md-button class="md-primary" v-on:click="active = false">Close</md-button>
            <md-button class="md-primary" v-on:click="Register">Submit</md-button>
          </md-dialog-actions>
        </md-dialog>
        <!-- ============================================== -->
      </div>
      <div class="md-layout-item" style="backgroundColor:#FFF; height:30px;"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      
      bottomPosition: 'md-bottom-right',
      img_path: "",
      active : false,
      view_info: false,
      submit:{
        name : "",
        email: "",
        contact : "",
        text :  "",
        homepage : "",
        host : "",
      }
    };
  },

  methods: {
    fileSelect(){
      console.log(this.$refs);
      this.img_path = this.$refs.img_path.files[0];
      console.log(this.img_path)
    },
      Register(){
        var data = new FormData();
        data.append('file',this.img_path)
        data.append('email',this.submit.email)
        this.$http
              .post("/supporter/assign",this.submit
              )
              .then(res => {
                  this.$http
              .post("/images/supporter/upload", data,{
                headers : {'Content-type' : 'multipart/form-data'
                }
              })
              .then(res => {
              alert("등록이 완료되었습니다.");
              this.$router.push({ name: 'home' });
              })
              .catch(err => {
              console.log("Submit failed");
              });
              })
              .catch(err => {
              console.log("Submit failed");
              });
        this.active = false;
        this.view_info = true;
      }

  }
};
</script>

<style lang="css">
 .md-dialog {
    max-width: 768px;
    min-height: 750px;
  }
   .demo-option {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   }

    .select {
      margin: 15px;
      display: inline-flex;
      width: auto;
    }

  .md-content {
    width: 100px;
    height: 100px;
    margin: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  
</style>



