<template>
  <div>
    <div class="md-layout" style="display: block; color:#F6F6F6">
      <div class="supportermain" style="min-height: 600px;">
        <div v-for="supporterinfo in  supporterlists" :key="supporterinfo.name">
          <div v-if="!supporterinfo.isAccepted">
            <md-card
              style="width: 40%; margin: 4px; marginTop: 50px; display: inline-block; vertical-align: top;"
            >
              <md-card-header>
                <md-card-header-text>
                  <div class="md-title" style="margin:auto; marginLeft: 25%; text-align:center;">
                    {{supporterinfo.name}}
                    <br />
                    <br />
                  </div>
                  <div class="md-subhead" style="text-align:left;font-weight: bold;">
                    # 이메일: {{supporterinfo.email}}
                    <br />
                    # 연락처: {{supporterinfo.contact}}
                    <br />
                    # 설명글: {{supporterinfo.text}}
                    <br />
                  </div>
                </md-card-header-text>
                <md-card-media md-medium>
                  <!-- <img v-bind:src="supporterinfo.photo_path" /> -->
                </md-card-media>
              </md-card-header>
              <md-card-actions>
                <md-button class="md-raised md-accent" v-on:click="Decline(supporterinfo)">Decline</md-button>
                <md-button class="md-raised md-primary" v-on:click="Accept(supporterinfo)">Accept</md-button>
              </md-card-actions>
            </md-card>
          </div>
        </div>
      </div>
      <div class="md-layout-item" style="backgroundColor:#FFF; height:30px;"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      supporterlists : {},
      submit:{
        name : "",
        email: "",
        contact : "",
        text :  "",
        img_path : "",
      }
    };
  },
created() {
          this
            .$http
            .get('/supporter')
            .then(response => {
              this.supporterlists = response.data; 
              console.log("Success")
              console.log(this.supporterlists)
            })
          },

      methods: {
      Accept(supporterinfo){
        this.$http
              .post("/supporter/accept", supporterinfo)
              .then(res => {
              alert("서포터 등록이 완료되었습니다.");
              this.$router.push({ name: 'home' });
              })
              .catch(err => {
              console.log("오류입니다. 잠시 후 다시 시도해주세요");
              });
      },
      Decline(supporterinfo){
          this.$http
              .post("/supporter/decline", supporterinfo)
              .then(res => {
              alert("서포터 해제가 완료되었습니다. 메인 페이지로 이동합니다.");
              this.$router.push({ name: 'home' });
              })
              .catch(err => {
              console.log("Finish Request failed");
              console.log("오류입니다. 잠시 후 다시 시도해주세요");
              });

      }
  }
      
};
</script>

<style lang="css">
 .md-dialog {
    max-width: 768px;
  }
   .demo-option {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   }

    .select {
      margin: 15px;
      display: inline-flex;
      width: auto;
    }
  
  
</style>



