<template>
  <div class="card-expansion" style="marginTop : 5% ;">
    <div v-for="Question in  Questions" :key="Question.email">
      <md-card>
        <md-card-header>
          <div class="md-title">
            #ID
            {{Question.email}}
            <br />
            <br />
          </div>
          <div class="md-subhead">님의 문의사항</div>
        </md-card-header>
        <md-card-expand>
          <md-card-actions md-alignment="space-between">
            <md-card-expand-trigger>
              <md-button class="md-accent">문의 내용 보기</md-button>
            </md-card-expand-trigger>
            <div>
              <md-button class="md-primary" v-on:click="Finished(Question)">처리 완료</md-button>
            </div>
          </md-card-actions>
          <md-card-expand-content>
            <md-card-content>{{Question.content}}</md-card-content>
          </md-card-expand-content>
        </md-card-expand>
      </md-card>
    </div>
  </div>
</template>


<script>
export default {
  name: 'CardExpansion',
  data() {
    return {
      Questions :{
        email : "",
        content: "",
      }
    };
  },
  created() {
          this
            .$http
            .get('/supporter/question')
            .then(response => {
              this.Questions = response.data; 
              console.log("Success")
            })
          },
  methods: {
      Finished(Question){
        this.$http
              .post("/supporter/questionFinished", Question)
              .then(res => {
              console.log("Finish Requst Success");
              alert("문의 사항이 해결되었습니다.");
              this.$router.push({ name: 'home' });
              })
              .catch(err => {
              console.log("Finish Request failed");
              console.log("오류입니다. 잠시 후 다시 시도해주세요");
              });
      }
  }
};
</script>

<style lang="scss" scoped>
  .card-expansion {
    height: 480px;
  }

  .md-card {
    width: 450px;
    margin: 10px;
    display: inline-block;
    vertical-align: top;
  }
</style>


